@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');

.project{
    width: 100%;
    min-height: 100vh;
    margin-top: 100px;
    padding-bottom: 100px;
    background-color: #E8E8E8;
}


h1{
    font-family: 'Montserrat', sans-serif;
    color:#99154e;
    padding-top: 0;
    margin-top:75px;
    margin-bottom: 100px;
    border-bottom: 8px solid #495464;
    display: inline-block;
}



.Card{
    margin: 15px;
    animation: fade 3s linear;
}

.Card1, .Card2 {
  display:flex;
   flex-direction:row;
    width:80%;
     margin-left:10%;
       margin-right:10%;
}


.gradient{
    color: #aad8d3
}

.image {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    backface-visibility: hidden;
  }
  
  .middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  
  .container:hover .image {
    opacity: 0.3;
  }
  
  .container:hover .middle {
    opacity: 1;
  }
  
  .text {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    color: #625772;
    font-size: 15px;
    padding: 16px 32px;
  }

  @media only screen and (max-width: 600px) {

    .Card{
        margin: 2px;
    }
    .Card1,.Card2{
      flex-direction: column;
      margin-top: 5px;
    }
  }