p{
    color:#495464;
    font-size: 60px;
    font-family: 'Montserrat', sans-serif;
    padding-top: 25vh;
    flex-direction:row;
     display:flex;
}

@media only screen and (max-width: 600px) {

    p{
        font-size: 25px;
    }
}
@media only screen and (max-width: 800px) {
    p{
        font-size: 30px;
    }

}
@media only screen and (max-width: 500px) {

    p{
        font-size: 22px;
    }
}