@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

html, body, #app{
    width: 100%;
    height: 100%;
    z-index: -1;
}
.video2{
    object-fit: cover;
    height: max-content;
    width: 100%;
    z-index: -1;
    position: absolute;
}   

.hero-container2{
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    object-fit: contain;
}

#eicon{
    font-size:100px;
     margin-bottom:0;
      padding-bottom:0;
}


h1{
    font-family: 'Montserrat', sans-serif;
    color:#99154e;
    padding-top: 0;
    margin-bottom: 50px;
    border-bottom: 8px solid #495464;
    display: inline-block;
}

h2{
    font-family: 'Montserrat', sans-serif;
    padding-top: 0;
    margin-bottom: 100px;
}
